import { months } from '../constans/constans';

export const formateDate = (date) => {
    const arr = date.split('-');

    let num = arr[2][0] === '0' ? arr[2][1] : arr[2]; //   0    10
    let month;

    for (let key in months){
        if(key === String(arr[1])){
            month = months[key] 
        }
    };

    return num + ' ' + month //     2021-01-24  24 
}

export const formateNum = num => Math.ceil(num) === '-0' ? '0' : Math.ceil(num); //    

export const formateNumToFixed = num => (num.toFixed(1)).replace('.', ','); //      